import React from 'react'
import { Container, Row, Image, Col } from 'react-bootstrap';

const UpImage = () => {
    return (
        <div>
           <Container>
                <Row>                   
                    <Col xs={6} md={4}>
                    <Image src="holder.js/171x180" roundedCircle />
                    </Col>                 
                </Row>
                </Container>
        </div>
    )
}

export default UpImage
